#organization {
	width: 100%;
	.inner {
		margin-left: -30px;
		margin-right: -30px;
		.aboutSectionCont {
			width: 100%;
			.organizationWrap {
				width: 100%;
				border-top: 1px solid $borderColor;
				.organizationList {
					border-bottom: 1px solid $borderColor;
					.organizationBox {
						display: flex;
						align-items: flex-end;
						.memberImgBox {
							width: 400px;
							height: 100%;
							padding: 30px;
							border-right: 1px solid $borderColor;
							.memberImg {
								width: 100%;
								height: 100%;
								img {
									width: 100%;
									height: 100%;
									object-fit: cover;
									filter: grayscale(1);
								}
							}
						}
						.memberDescBox {
							width: 70%;
							padding: 30px;
							.memberPosition {
								@include sectionSubTitle($subLetterS: 1px, $fontWeight: 400);
								text-transform: uppercase;
								margin-bottom: 20px;
							}
							.memberName,
							.memberTeam,
							.memberRank {
								@include sectionSubTitle($subFontColor: $borderColor, $subFz: 20px);
								margin-bottom: 4px;
							}
						}
					}
					&:last-child {
						border-bottom: 0;
					}
				}
			}
		}
	}
}
#location {
	width: 80%;
	margin-left: 20%;
	margin-bottom: 80px;
	#map {
		//width: 80%;
		height: 400px;
		border: 1px solid $borderColor;
		filter: saturate(0%) invert(100%);
	}
	.mapTxt {
		width: 60%;
		margin-top: 20px;
		p {
			@include sectionText;
		}
	}
	.mapBtn {
		display: flex;
		align-items: flex-start;
		margin-top: 30px;
		.mapBtnTitle {
			width: 15%;
			@include sectionText($captionFz: 16px, $captionFontColor: $textColor);
		}
		.mapBtnLists {
			width: 85%;
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			.mapBtnList {
				width: 49%;
				margin-right: 2%;
				padding: 20px 0;
				border-top: 2px solid $hoverColor;
				margin-bottom: 10px;
				.listBox {
					.listTitle {
						@include sectionText($captionFz: 14px, $captionFontColor: $textColor);
						margin-bottom: 30px;
					}
					.traffic {
						display: flex;
						align-items: center;
						.trafficList {
							position: relative;
							transition: 0.5s;
							&::after {
								content: '';
								position: absolute;
								left: 0;
								bottom: 0;
								width: 0;
								height: 1px;
								background-color: $borderColor;
								transition: 0.5s;
							}
							a {
								display: block;
								width: 100%;
								height: 100%;
								@include sectionText($captionFz: 12px, $captionFontColor: $textColor);
							}
							&:hover {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
							&.on {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
						}
						.trafficList + .trafficList {
							margin-left: 20px;
						}
					}
					.roadView {
						display: flex;
						align-items: center;
						.roadViewList {
							position: relative;
							transition: 0.5s;
							&::after {
								content: '';
								position: absolute;
								left: 0;
								bottom: 0;
								width: 0;
								height: 1px;
								background-color: $borderColor;
								transition: 0.5s;
							}
							a {
								display: block;
								width: 100%;
								height: 100%;
								@include sectionText($captionFz: 12px, $captionFontColor: $textColor);
							}
							&:hover {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
							&.on {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
						}
						.roadViewList + .roadViewList {
							margin-left: 20px;
						}
					}
					.favLocation {
						display: flex;
						align-items: center;
						.locaList {
							position: relative;
							transition: 0.5s;
							&::after {
								content: '';
								position: absolute;
								left: 0;
								bottom: 0;
								width: 0;
								height: 1px;
								background-color: $borderColor;
								transition: 0.5s;
							}
							a {
								display: block;
								width: 100%;
								height: 100%;
								@include sectionText($captionFz: 12px, $captionFontColor: $textColor);
							}
							&:hover {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
							&.on {
								&::after {
									width: 100%;
									background-color: $hoverColor;
								}
								a {
									@include sectionText($captionFz: 12px, $captionFontColor: $hoverColor);
								}
							}
						}
						.locaList + .locaList {
							margin-left: 20px;
						}
					}
				}
				&:nth-of-type(even) {
					margin-right: 0;
				}
				&:nth-of-type(3) {
					width: 100%;
					margin-right: 0;
				}
			}
		}
	}
}

